:root {
    --zolo-separator-space-between: 20px;
    --zolo-separator-size: 4px;
    --zolo--countdown-gap: 10px;
}

.wp-block-zolo-countdown {
    --zolo-countdown-wrap-padding: 0 15px;
    --zolo-item-flex-width: 180px;
    --zolo-item-flex-height: 180px;
    --zolo-item-border-width: 1px;
    --zolo-item-border-style: solid;
    --zolo-item-border-color: #f0f0f0;
    --zolo-item-gap: 5px;
    --zolo-item-border-radius: 4px;
    --zolo-face-text-color: #45444e;
    --zolo-label-text-color: #797987;
    --zolo-item-after-margin: 0 -9px 0 0;
    --zolo-face-padding: 15px;
    --zolo-label-padding: 15px;

    --zolo-face-border-radius: 6px;
    --zolo-face-border-width: 1px;
    --zolo-face-border-color: #eaeaea;
    --zolo-face-border-style: solid;
    --zolo-label-border-radius: 6px;
    --zolo-label-border-width: 1px;
    --zolo-label-border-color: #eaeaea;
    --zolo-label-border-style: solid;

    display: flow-root;
    box-sizing: content-box;
    margin-left: auto;
    margin-right: auto;
    padding: var(--zolo-countdown-wrap-padding);

    .zolo-countdown-wrap {
        display: grid;
        justify-content: center;
        align-items: center;
        gap: var(--zolo--countdown-gap);
        grid-template-columns: repeat(2, 1fr);
        &.flex {
            .zolo-countdown-item {
                width: var(--zolo-item-flex-width);
                height: var(--zolo-item-flex-height);
                min-width: var(--zolo-item-flex-width);
            }
            &.zolo-countdown-style-2,
            &.zolo-countdown-style-3 {
                .zolo-countdown-item {
                    height: auto;
                    justify-content: space-between;
                }
            }
        }

        // &.grid {
