    }

    .zolo-countdown-item {
        text-align: center;
        position: relative;
        border-width: var(--zolo-item-border-width);
        border-style: var(--zolo-item-border-style);
        border-color: var(--zolo-item-border-color);
        border-radius: var(--zolo-item-border-radius);
        display: flex;
        flex-direction: column;
        justify-content: center;
        box-sizing: border-box;
        gap: var(--zolo-item-gap);
    }

    .zolo-countdown-face {
        font-size: 3rem;
        color: var(--zolo-face-text-color);
        line-height: 1;
    }

    .zolo-countdown-label {
        font-size: 15px;
        color: var(--zolo-label-text-color);
    }

    .zolo-countdown-item::after {
        position: absolute;
        content: ':';
        right: calc(-1 * var(--zolo--countdown-gap) / 2);
        margin: var(--zolo-item-after-margin);
        top: 50%;
        transform: translateY(-50%);
        font-style: normal;
        font-size: 60px;
        font-family: auto;
    }

    .zolo-countdown-item:nth-last-child(1)::after {
        display: none;
    }

    .zolo-countdown-style-1 .zolo-countdown-item,
    .zolo-countdown-style-3 .zolo-countdown-item,
    .zolo-countdown-style-4 .zolo-countdown-item {
        border: 0;
    }

    .zolo-countdown-style-2 .zolo-countdown-face {
        padding: var(--zolo-face-padding);
    }

    .zolo-countdown-style-2 .zolo-countdown-label {
        padding: var(--zolo-label-padding);
    }

    .zolo-countdown-style-2 .zolo-countdown-label {
        background-color: #ededed;
    }
    .zolo-countdown-style-2 .zolo-countdown-item {
        --zolo-item-gap: 0;
        gap: var(--zolo-item-gap);
    }

    .zolo-countdown-style-3 .zolo-countdown-face {
        padding: var(--zolo-face-padding);
        background-color: #fff;
        border-radius: var(--zolo-face-border-radius);
        border-width: var(--zolo-face-border-width);
        border-color: var(--zolo-face-border-color);
        border-style: var(--zolo-face-border-style);
    }

    .zolo-countdown-style-3 .zolo-countdown-label {
        --zolo-label-padding: 10px;
        background-color: #fafafa;
        padding: var(--zolo-label-padding);
        border-radius: var(--zolo-label-border-radius);
        border-width: var(--zolo-label-border-width);
        border-color: var(--zolo-label-border-color);
        border-style: var(--zolo-label-border-style);
    }

    .zolo-countdown-style-4 .zolo-countdown-item {
        --zolo-item-gap: 5px;
        display: flex;
        gap: var(--zolo-item-gap);
        align-items: center;
        justify-content: flex-start;
        flex-direction: row;
        height: auto !important;
    }

    .zolo-countdown-style-4 .zolo-countdown-face {
        font-size: 2rem;
    }


}


@media (min-width: 768px) {
    .wp-block-zolo-countdown {
        .zolo-countdown-wrap {
            grid-template-columns: repeat(4, 1fr);
        }
    }
}


@media (max-width: 767px) {
    .wp-block-zolo-countdown {
        .zolo-countdown-wrap {
            &.grid {
                &.zolo-mbgc-1 {
                    .zolo-countdown-item {
                        &::after {
                            display: none;
                        }
                    }
                }
                &.zolo-mbgc-2{
                    .zolo-countdown-item {
                        &:nth-child(even){
                            &::after {
                                display: none;
                            }
                        }
                    }
                }
                &.zolo-mbgc-3{
                    .zolo-countdown-item {
                        &:nth-child(3n+3){
                            &::after {
                                display: none;
                            }
                        }
                    }
                }
                &.zolo-mbgc-4{
                    .zolo-countdown-item {
                        &:nth-child(4n+4){
                            &::after {
                                display: none;
                            }
                        }
                    }
                }
                &.zolo-mbgc-5{
                    .zolo-countdown-item {
                        &:nth-child(5n+5){
                            &::after {
                                display: none;
                            }
                        }
                    }
                }
                &.zolo-mbgc-6{
                    .zolo-countdown-item {
                        &:nth-child(6n+6){
                            &::after {
                                display: none;
                            }
                        }
                    }
                }
                &.zolo-mbgc-7{
                    .zolo-countdown-item {
                        &:nth-child(7n+7){
                            &::after {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
}

